 i.Nav {
    background-color: #000;
    border-bottom: 1px solid rgb(0, 0, 0);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
    -webkit-transition: height 0.2s ease-in-out;
    transition: height 0.2s ease-in-out;
    height: 77px;
  }
  .Nav-Menus {
    display: flex;
    flex-direction: row;
    height: 77px;
    width: 70%;
    margin: 0 auto;
    padding: 26px 40px;
  }
  .Nav-Brand-Logo {
    display: block;
    background-position: -176px 0px;
    background-image: url(../../sprite.png);
    background-size: 405px 379px;
    background-repeat: no-repeat;
    height: 35px;
    width: 176px;
    text-indent: -1000%
  }

  .navbar-fixed-top {
    border: 0;
    background-color: rgb(212, 175, 55);
    border-bottom: 1px solid rgba(252, 244, 244, 0);
    font-family: "Brush Script MT";
    font-weight: bolder;
    font-size: large;
    color: #000;
  }